import AMapLoader from "@amap/amap-jsapi-loader";

/**
 * get location
 * @param type
 * @returns Promise
 */
export function getLocation(type: "location" | "place" = "location") {
    return new Promise<any>((resolve, reject) => {
        AMapLoader.load({
            key: process.env.VUE_APP_GKEY as string, // WebKey load 
            version: "1.4.15", //  JSAPI  1.4.15
        })
            .then((AMap) => {
                AMap.plugin("AMap.Geolocation", function() {
                    const geolocation = new AMap.Geolocation({
                        // true
                        enableHighAccuracy: true,
                        // 
                        timeout: 0,
                    });

                    geolocation[type === "place" ? "getCityInfo" : "getCurrentPosition"](function(
                        status: string,
                        result: any
                    ) {
                        if (status == "complete") {
                            onComplete(result);
                        } else {
                            onError(result);
                        }
                    });
                    function onComplete(data: any) {
                        resolve(data);
                    }
                    function onError(e: any) {
                        // 
                        reject(e);
                    }
                });
            })
            .catch((e) => {
                reject(e);
            });
    });
}
